
"use client";

import { useState } from "react";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { users as initialUsers, User, roleNames } from "@/lib/constants";
import { CreateUserDialog } from "./create-user-dialog";
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu";
import { MoreHorizontal } from "lucide-react";
import { useToast } from "@/hooks/use-toast";

export function UserManagementTable() {
  const [users, setUsers] = useState(initialUsers);
  const [isDialogOpen, setDialogOpen] = useState(false);
  const [editingUser, setEditingUser] = useState<User | null>(null);
  const { toast } = useToast();

  const handleCreateClick = () => {
    setEditingUser(null);
    setDialogOpen(true);
  }

  const handleEditClick = (user: User) => {
    setEditingUser(user);
    setDialogOpen(true);
  }

  const handleSaveUser = (savedUser: User, originalEmail?: string) => {
    if (editingUser && originalEmail) {
        // Update existing user
        setUsers(users.map(u => u.email === originalEmail ? {...u, ...savedUser} : u));
        toast({ title: "User Updated", description: `Details for ${savedUser.name} have been updated.`})
    } else {
        // Add new user
        if(!savedUser.password) {
            toast({ variant: "destructive", title: "Password Required", description: "A password must be set for new users."})
            return;
        }
        setUsers([...users, savedUser]);
        toast({ title: "User Created", description: `${savedUser.name} has been added.`})
    }
  };

  return (
    <>
      <div className="flex justify-end mb-4">
        <Button onClick={handleCreateClick}>Create User</Button>
      </div>
      <Table>
        <TableHeader>
          <TableRow>
            <TableHead>User</TableHead>
            <TableHead>Email</TableHead>
            <TableHead>Role</TableHead>
            <TableHead><span className="sr-only">Actions</span></TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {users.map((user) => (
            <TableRow key={user.email}>
              <TableCell className="font-medium flex items-center gap-3">
                <Avatar>
                  <AvatarImage src={`https://placehold.co/40x40.png?text=${user.name.charAt(0)}`} alt={user.name} data-ai-hint="person" />
                  <AvatarFallback>{user.name.charAt(0)}</AvatarFallback>
                </Avatar>
                {user.name}
              </TableCell>
              <TableCell>{user.email}</TableCell>
              <TableCell>
                <Badge variant="secondary">{roleNames[user.role]}</Badge>
              </TableCell>
              <TableCell className="text-right">
                 <DropdownMenu>
                    <DropdownMenuTrigger asChild>
                      <Button variant="ghost" className="h-8 w-8 p-0">
                        <span className="sr-only">Open menu</span>
                        <MoreHorizontal className="h-4 w-4" />
                      </Button>
                    </DropdownMenuTrigger>
                    <DropdownMenuContent align="end">
                      <DropdownMenuItem onClick={() => handleEditClick(user)}>Edit</DropdownMenuItem>
                      <DropdownMenuItem className="text-destructive">Delete</DropdownMenuItem>
                    </DropdownMenuContent>
                  </DropdownMenu>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <CreateUserDialog
        isOpen={isDialogOpen}
        onClose={() => setDialogOpen(false)}
        onUserSaved={handleSaveUser}
        userToEdit={editingUser}
      />
    </>
  );
}
